.skillsBlock {
    min-height: 100vh;
    background-color: rgb(26, 37, 42);

}

.skillsContainer {
    justify-content: flex-start;
    flex-direction: column;
    flex-wrap: nowrap;
}

.title {
    background-color: darkslategray;

}

.skills {
    width: 100%;
    border: 1px solid;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: flex-start;
}